.menu__list {
  @include reset-list;
}

.menu__item {
  box-shadow: 0 1px 0 0 #ececec, inset 0 1px 0 0 #ececec;
  padding: 32px 0;
  padding-left: 24%;
}

.menu__item-search {
  &::placeholder {
    font-size: 17px;
    font-weight: bold;
    line-height: 1.41;
    color: #000000;
    text-overflow: ellipsis;
  }
  font-size: 17px;
  font-weight: bold;
  line-height: 1.41;
  color: #000000;
  text-overflow: ellipsis;
  border: none;
  width: 75%;
}

.menu__link {
  font-size: 17px;
  font-weight: bold;
  line-height: 1.41;
  text-decoration: none;
  color: #000000;
}

.menu__btn {
  position: absolute;
  top: 30px;
  right: 0;
}
